import React, { FC } from "react"
import ForWardRefImg from "../src/assets/gif/forwardRef.gif"
import { Tabs } from "antd"
import { UnControlled as CodeMirror } from "react-codemirror2"
import "./index.scss"
const App: FC = (props) => {
  return (
    <>
      <Tabs
        defaultActiveKey='item-2'
        tabPosition={"left"}
        items={[
          {
            label: "",
            key: "item-1",
            children: "react,ts, ",
          },
          {
            label: "props",
            key: "item-2",
            children: (
              <div>
                :{" "}
                <CodeMirror
                  value={`import React from 'react'
const App = ( ) => {
  return <Child message=''/>
}
export default App`}
                  options={{
                    mode: "javascript",
                    theme: "material",
                    lineNumbers: true,
                  }}
                />
                <br />
                :
                <CodeMirror
                  value={`import React, { FC } from "react"
interface Props {
  message: string
}
const Child: FC<Props> = ( props ) => {
  return <div>{ props.message }</div>
}
export default Child`}
                  options={{
                    mode: "javascript",
                    theme: "material",
                    lineNumbers: true,
                  }}
                />
              </div>
            ),
          },

          {
            label: "useState",
            key: "item-3",
            children: (
              <CodeMirror
                value={`import React, { useState, FC } from 'react'
const App: FC = () => {
  const [count, setCountt] = useState<number>(0)
  return <div onClick={() => setCountt(count + 1)}>{count}</div>
}
export default App`}
                options={{
                  mode: "javascript",
                  theme: "material",
                  lineNumbers: true,
                }}
              />
            ),
          },
          {
            label: "ref",
            key: "item-4",
            children: (
              <CodeMirror
                value={`import React, { useRef } from "react"
const App = ( ) => {
  const ref = useRef<HTMLDivElement>(null)
  const btnRef = useRef<HTMLButtonElement>(null)
  return (
    <>
      <div
        ref={ref}
        onClick={() => {
          if (btnRef.current != null) {
            btnRef.current.click()
          }
        }}
      >
        ww
      </div>
      <button ref={btnRef} onClick={() => console.log(btnRef.current)}>
        click me
      </button>
    </>
  )
}
export default App`}
                options={{
                  mode: "javascript",
                  theme: "material",
                  lineNumbers: true,
                }}
              />
            ),
          },
          {
            label: "forwordRef",
            key: "item-5",
            children: (
              <div>
                React ref
                
                <br />
                :
                <CodeMirror
                  value={`import React, { forwardRef, RefObject, ReactElement } from 'react'
const ForwardRef = forwardRef<
  HTMLButtonElement,
  {
    children: ReactElement
    ref: RefObject<HTMLButtonElement>
    onClick: () => void
    message: string
  }
>((props, ref) => (
  <>
  <div>
   <span>{props.message}</span>
   <button ref={ref} onClick={() => console.log('')}>
    {props.children}
   </button>
</div>
  </>
))
export default ForwardRef
                  `}
                  options={{
                    mode: "javascript",
                    theme: "material",
                    lineNumbers: true,
                  }}
                />
                :
                <CodeMirror
                  value={`import { useRef } from 'react'
import ForwardRef from "./component/ForwardRef"
const App: FC = ( ) => {
const ref = useRef<HTMLButtonElement>(null)
  return (
    <button
      onClick={() => {
        if (ref.current !== null) {
        ref.current.click()
        }
      }}
    >
      ,
    </button>
  )
}
<ForwardRef message='msg' ref={ref}>
   <div> click me!</div>
</ForwardRef>`}
                  options={{
                    mode: "javascript",
                    theme: "material",
                    lineNumbers: true,
                  }}
                />
                :
                <br />
                <img src={ForWardRefImg} alt='' />
              </div>
            ),
          },
          {
            label: "useContext",
            key: "item-6",
            children: (
              <div>
                useContext: ,props
                <br />
                context,
                <CodeMirror
                  value={`import { createContext } from 'react'
interface Themes {
  foreground: String
  background: String
}
const themes = {
    foreground: '#000000',
    background: '#eeeeee',
}
export const Context = createContext<Themes>(themes)`}
                  options={{
                    mode: "javascript",
                    theme: "material",
                    lineNumbers: true,
                  }}
                />
                <br />
                :
                <CodeMirror
                  value={`import React, { FC } from "react"
import { Context, themes } from "./context/index"
const App = ( ) => {
  return (
    // value
    <Context.Provider value={themes}>
      <Child></Child>
    </Context.Provider>
  )
}
export default App`}
                  options={{
                    mode: "javascript",
                    theme: "material",
                    lineNumbers: true,
                  }}
                />
                <br />
                :
                <CodeMirror
                  value={`import React, { FC } from "react"
const Child = ( ) => {
  return (
    // 
    <Son></Son>
  )
}
export default Child`}
                  options={{
                    mode: "javascript",
                    theme: "material",
                    lineNumbers: true,
                  }}
                />
                <br />
                :
                <CodeMirror
                  value={`import React, { useContext } from "react"
import { Context } from "../context"
const Son = ( ) => {
  const themes = useContext(Context)
  return (
    const themes = useContext(Context)
    return <div>{themes.foreground}</div>
  )
}
export default Son`}
                  options={{
                    mode: "javascript",
                    theme: "material",
                    lineNumbers: true,
                  }}
                />
              </div>
            ),
          },
        ]}
      ></Tabs>
    </>
  )
}

export default App
